<template>
  <li>
    <form @submit.prevent="handleSubmit">
      <div class="grid grid--bleed">
        <div class="grid__col-sm-6 grid__col--bleed-x">
          <input
            type="text"
            placeholder="Category"
            v-model="bill.category"
          />
        </div>

        <div class="grid__col-sm-4 grid__col--bleed-x">
          <db-money
            :model="bill.amount"
            @changed="(v) => bill.amount = v"
          />
        </div>

        <div class="grid__col-sm-2 grid__col--bleed-x">
          <button>{{ buttonText }}</button>
        </div>
      </div>
    </form>
  </li>
</template>

<script>
  import dbMoney from './utils/Money';

  export default {
    props: ['bill', 'buttonText'],

    components: {
      dbMoney,
    },

    methods: {
      handleSubmit () {
        this.$emit("submit", this.bill);
      },
    },
  };
</script>

<style lang="scss" scoped>
  li {
    margin: 0;
    padding: 0.5rem;
    border-bottom: 1px solid rgba(0, 0, 0, 0.125);
  }

  .header button {
    color: #EDD350;
    border-color: #EDD350;
  }

  button {
    padding: 0.5rem 1.5rem;
    font-size: 0.9rem;
    color: #8B7400;
    background: none;
    cursor: pointer;
    border: 3px solid #8B7400;
    border-radius: 50vh;
    font-weight: bold;
    box-shadow: 0.1rem 0.1rem 0.05rem rgba(0, 0, 0, 0.2);
    transition: background-color 0.2s;
    outline: 0;

    &::selection {
      background: none;
    }

    &:hover {
      background: darken(#FFE97E, 35%);
    }

    &:active {
      transform: translate(2px, 2px);
    }
  }

  input {
    font-size: 1.2rem;
    padding: 0.5rem;
    width: 100%;
  }
</style>



