<template>
  <form @submit.prevent="$router.push({ path: '/pay_period/1' })">
    <label>
      First pay day this month:
      <input type="date" v-model="payDay1.format('YYYY-MM-DD')" />
    </label>

    <label>
      Second pay day this month:
      <input type="date" v-model="payDay2.format('YYYY-MM-DD')" />
    </label>

    <label>
      Take-home amount each pay day:
      <db-money
        :model="income"
        @changed="(v) => income = v"
      />
    </label>

    <input type="submit" value="Next" />
  </form>
</template>

<script>
  import { Settings } from './main';
  import Storage from './utils/Storage';
  import dbMoney from './utils/Money';

  export default {
    data () {
      return Settings;
    },

    components: {
      dbMoney,
    },
  };
</script>

<style lang="scss" scoped>
  form {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    label {
      display: block;
      margin: 0 0 1rem;
    }

    input {
      width: 100%;
      font-size: 1.2rem;
      padding: 0.5rem;
      border-radius: 0.2rem;
      border: 1px solid rgba(0, 0, 0, 0.3);
      display: block;
    }
  }

  input[type='submit'] {
    padding: 0.5rem 1.5rem;
    font-size: 1.2rem;
    background: MediumSeaGreen;
    color: white;
    cursor: pointer;
    border: none;
    border-radius: 0.2rem;
    font-weight: bold;
    text-shadow: 0.05rem 0.05rem 0 rgba(0, 0, 0, 0.5);
    box-shadow: 0.1rem 0.1rem 0.05rem rgba(0, 0, 0, 0.2);
    transition: background-color 0.2s;
    outline: 0;

    &::selection {
      background: none;
    }

    &:hover {
      background: darken(#3cb371, 15%);
    }

    &:active {
      transform: translate(2px, 2px);
    }
  }

  @media (max-width: 375px) {
    form {
      width: 100vw;
      position: relative;
      transform: none;
      top: 0;
      left: 0;
      padding: 1rem;
    }
  }
</style>



