<template>
  <div class="main">
    <div class="header">
      <h1>{{ title }}</h1>
      <h2>{{ subTitle }}</h2>

      <div class="grid grid--bleed grid--justify-center">
        <div class="grid__col-sm-8 grid__col--bleed">
          <ul>
            <db-bill-form
              ref="newBillForm"
              buttonText="+ Add"
              :bill="newBill"
              @submit="handleNewBillSubmit"
            />
          </ul>
        </div>
      </div>
    </div>

    <div class="container grid grid--justify-center">
      <div class="grid__col-sm-8">
        <ul>
          <db-bill-form
            buttonText="Save"
            v-for="bill in bills"
            v-if="!!bill.isEditing"
            :ref="'billForm' + bill.id"
            :bill="bill"
            :key="bill.id"
            @submit="handleSubmit"
          />

          <li
            :key="bill.id"
            v-else
          >
            <div class="grid grid--bleed">
              <div class="grid__col-sm-6 grid__col--bleed-x">
                {{ bill.category }}
              </div>

              <div class="grid__col-sm-4 grid__col--bleed-x">
                {{ bill.amount | to-currency }}
              </div>

              <div class="grid__col-sm-2 grid__col--bleed-x">
                <icon
                  name="edit"
                  @click.native="editBill(bill.id)"
                />

                <icon
                  class="icon--red"
                  name="times"
                  @click.native="removeBill(bill.id)"
                />
              </div>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  import 'vue-awesome/icons';
  import Icon from 'vue-awesome/components/Icon';

  import dbBillForm from './BillForm';

  var Bill = function(category, amount, payPeriod, id) {
    this.category = category || "";
    this.amount = amount || 0;
    this.payPeriod = parseInt(payPeriod);
    this.id = id;
    this.isEditing = false;

    this.isValid = function() {
      return this.category != "" &&
               this.amount != 0 &&
                 this.payPeriod != undefined;
    };
  };

  export default {
    data() {
      return {
        bills: this.initialBills.filter((b) => {
          return parseInt(b.payPeriod) == parseInt(this.payPeriod);
        }),
        newBill: new Bill("", 0, this.payPeriod),
      };
    },

    watch: {
      payPeriod (v) {
        this.bills = this.initialBills.filter((b) => {
          return parseInt(b.payPeriod) == parseInt(v);
        });
      },
    },

    computed: {
      nextKey () {
        return this.bills.length;
      },
    },

    props: [
      "initialBills",
      "title",
      "subTitle",
      "prevSection",
      "nextSection",
      "payPeriod",
    ],

    components: {
      Icon,
      dbBillForm,
    },

    methods: {
      handleNewBillSubmit (bill) {
        if (!!bill && bill.isValid()) {
          bill.id = this.nextKey;
          bill.isEditing = false;
          this.bills.unshift(bill);
          this.$refs.newBillForm.$el.querySelector("input").focus();
          this.newBill = new Bill("", 0, this.payPeriod);
        }
      },

      handleSubmit (bill) {
        var editingBill = new Bill(
          bill.category,
          bill.amount,
          bill.payPeriod,
          bill.id,
        );

        if (editingBill.isValid())
          bill.isEditing = false;
      },

      removeBill (id) {
        var idx = this.bills.findIndex((bill) => {
          return bill.id == id;
        });
        this.bills.splice(idx, 1);
      },

      editBill (id) {
        var idx = this.bills.findIndex((bill) => {
          return bill.id == id;
        });

        this.bills[idx].isEditing = true;
      },
    },

    mounted () {
      this.$refs.newBillForm.$el.querySelector("input").focus();
    },
  };
</script>

<style lang="scss" scoped>
  .header {
    position: fixed;
    width: 100vw;
    background: linear-gradient(
      to bottom,
      rgba(109, 33, 148, 1) 0%,
      rgba(152, 94, 182, 1) 100%
    );
    box-shadow: 0 0.2rem 0.3rem rgba(109, 33, 148, 0.7);
    z-index: 2;
  }

  .container {
    padding-top: 160px;
    padding-bottom: 3rem;
    position: relative;
    z-index: 1;
  }

  h1,
  h2 {
    color: white;
    margin: 1rem 0 0;
    text-align: center;
    text-shadow: 0.1rem 0.1rem 0.5rem rgba(0, 0, 0, 0.5);
  }

  h1 {
    margin-bottom: 0.5rem;
    font-size: 1.6rem;
  }

  h2 {
    font-size: 0.9rem;
  }

  ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }

  li {
    margin: 0;
    padding: 0.5rem;
    border-bottom: 1px solid rgba(0, 0, 0, 0.125);

    &:hover {
      .fa-icon {
        opacity: 1;
      }
    }
  }

  .header li {
    border: none;
  }

  .fa-icon {
    cursor: pointer;
    padding: 0 0.5rem;
    opacity: 0;
    transition: opacity 0.2s;
  }

  .icon--red {
    color: red;
  };
</style>



