<template>
  <div>
    <router-view />

    <div v-if="welcome.active" class="footer">
      <router-link
        v-if="!!welcome.prev"
        :to="welcome.prev.path"
        class="btn footer__back-btn"
      >
        go back to {{ welcome.prev.name }}
      </router-link>

      <router-link
        v-if="!!welcome.next"
        :to="welcome.next.path"
        class="btn"
      >
        Next: {{ welcome.next.name }} &rarr;
      </router-link>
    </div>
  </div>
</template>

<script>
  import welcome from './welcome';
  import WebFont from 'webfontloader';

  WebFont.load({
    google: {
      families: ['Roboto'],
    },
  });

  export default {
    data () {
      return {
        welcome,
      };
    },
  };
</script>

<style lang="scss">
  html {
    box-sizing: border-box;
    font-family: 'Roboto', Helvetica Neue, sans-serif;
  }

  *,
  *:before,
  *:after {
    box-sizing: inherit;
  }

  .fa-icon {
    box-sizing: content-box;
  }

  body,
  html {
    margin: 0;
    padding: 0;
    height: 100vh;
  }

  .grid {
    display: flex;
    flex-flow: row nowrap;
    padding: 1rem;

    &.grid--justify-center {
      justify-content: center;
    }

    &.grid--bleed {
      padding: 0;
    }
  }

  [class*="grid__col-"] {
    margin-right: 1rem;
    padding: 1rem;

    &:last-child,
    &:only-child {
      margin-right: 0;
    }

    &.grid__col-auto {
      flex: 1 1;
    }

    &.grid__col--align-self-center {
      align-self: center;
    }

    &.grid__col--bleed-x {
      padding: 1rem 0;
    }

    &.grid__col--bleed {
      padding: 0;
    }
  }

  @media (max-width: 400px) {
    .grid {
      flex-flow: column nowrap;
    }
  }

  @media (min-width: 400px) {
    .grid__col-sm-8 {
      width: 75%;
    }

    .grid__col-sm-6 {
      width: 50%;
    }

    .grid__col-sm-4 {
      width: 33.333333333%;
    }

    .grid__col-sm-2 {
      width: 16.6666666667%;
    }
  }

  .footer {
    position: fixed;
    z-index: 2;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 0.5rem 1rem;
    background: rgba(152, 94, 182, 1);
    border-top: 1px solid rgba(0, 0, 0, 0.2);
    box-shadow: 0 -0.2rem 0.2rem rgba(0, 0, 0, 0.2);
    text-align: right;

    .btn {
      text-decoration: none;
      padding: 0.3rem 0.5rem;
      font-size: 0.8rem;
      color: #EDD350;
      background: none;
      cursor: pointer;
      border: 3px solid #EDD350;
      border-radius: 50vh;
      font-weight: bold;
      box-shadow: 0.1rem 0.1rem 0.05rem rgba(0, 0, 0, 0.2);
      transition: background-color 0.2s;
      outline: 0;

      &.footer__back-btn {
        font-size: 0.7rem;
        border: none;
        border-radius: none;
        box-shadow: none;
        float: left;

        &:hover {
          background: none;
          text-decoration: underline;
        }
      }

      &::selection {
        background: none;
      }

      &:hover {
        background: darken(#FFE97E, 35%);
      }

      &:active {
        transform: translate(2px, 2px);
      }
    }
  }
</style>



