<template>
  <db-bill-entry
    title="Bills during 1st pay period"
    pay-period="1"
    :sub-title="subTitle"
    :initialBills="payDayBills"
  />
</template>

<script>
  import welcome from './welcome';
  import { Settings } from './main';
  import Storage from './utils/Storage';
  import dbBillEntry from './BillEntry';

  export default {
    data() {
      return {
        payDayBills: Storage.get(
          "payDayBills",
          {
            parser: "JSON",
            default: [],
          }
        ),
      };
    },

    components: {
      dbBillEntry,
    },

    computed: {
      subTitle () {
        var start = Settings.payDay1.format("dddd, MMM Do"),
            ending = Settings.payDay2.subtract(1, "days")
                                     .format("dddd, MMM Do");

        return start + " – " + ending;
      },
    },

    watch: {
      payDayBills: {
        deep: true,
        handler (current) {
          Storage.set("payDayBills", current, { formatter: "JSON" });
        },
      },
    },

    mounted () {
      welcome.activate({
        prev: {
          path: "/",
          name: "Income settings",
        },

        next: {
          path: "/pay_period/2",
          name: "2nd pay period",
        },
      });
    },

    destroyed () {
      welcome.reset();
    },
  };
</script>

<style lang="scss" scoped>
</style>



