import Vue from 'vue';

export default new Vue({
  data () {
    return {
      active: false,
      prev: {},
      next: {},
    };
  },

  methods: {
    activate (opts) {
      opts = Object.assign({}, {
        active: true,
        prev: false,
        next: false,
      }, opts);

      Object.keys(opts).forEach((k) => {
        this[k] = opts[k];
      });
    },

    reset () {
      this.activate({ active: false });
    },
  },
});



