import IncomeSettings from './IncomeSettings';
import PayPeriod1 from './PayPeriod1';
import PayPeriod2 from './PayPeriod2';
import Dashboard from './Dashboard';

export const routes = [
  { path: '/', component: IncomeSettings },
  { path: '/pay_period/1', component: PayPeriod1 },
  { path: '/pay_period/2', component: PayPeriod2 },
  { path: '/dashboard', component: Dashboard },
];



