<template>
  <money v-bind="money" v-model="value" />
</template>

<script>
  import { Money } from 'v-money';

  export default {
    data () {
      return {
        value: this.model,

        money: {
          decimal: '.',
          thousands: ',',
          prefix: '$',
          suffix: '',
          precision: 2,
          masked: false,
        },
      };
    },

    props: ["model"],

    components: {
      Money,
    },

    watch: {
      value (v) {
        this.$emit("changed", v);
      },
    },
  };
</script>



